import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

@Injectable()
export class AuthenticationService {

    private url = 'http://13.126.120.22:8080/notous';

    constructor(private http: HttpClient) { }

    login(userForm) {
        console.log(userForm);
        return this.http.post<any>(this.url+'/api/v1/auth', {'email':userForm.get('username').value,'password':userForm.get('password').value})
            .map(user => {
                console.log("user: "+JSON.stringify(user));
                if (user && user.token) {
                    localStorage.setItem('currentUser', JSON.stringify(user));
                }
                return user;
            });
    }

    logout() {
        localStorage.removeItem('currentUser');
    }

    

    resetPassword(userForm) {
        return this.http.post<any>(this.url+'/api/v1/updatePassword', {'email':userForm.get('email').value,'password':userForm.get('password').value, 'OTP':userForm.get('otp').value,'contact':userForm.get('contact').value})
            .map(user => {
                console.log("user: "+JSON.stringify(user));
                if (user && user.token) {
                    localStorage.setItem('currentUser', JSON.stringify(user));
                }
               return user;
            });
    }

    sendOTP(userForm) {
        return this.http.post<any>(this.url+'/api/v1/resetPassword', {'email':userForm.get('email').value,'contact':userForm.get('contact').value})
            .map(user => {
                console.log("user: "+JSON.stringify(user));
                if (user && user.token) {
                    localStorage.setItem('currentUser', JSON.stringify(user));
                }
               return user;
            });
    }
}


